
.more{
	border-bottom: 1px solid #f0f0f0;
	position: relative;
}
.more a{
	width: 100%;
	height: 0.8rem;
	display: inline-block;
	background: url(../../../assets/fonts/ms.png) no-repeat;
	background-size: contain;
}
.time{
	position: absolute;
	top: 0.16rem;
	left: 2.666667rem;
	font-size: 0.373333rem;
}
.time span{
	display: inline-block;
	width: 0.533333rem;
	height: 0.533333rem;
	background: #000;
	color: #fff;
	text-align: center;
	line-height: 0.533333rem;
}
.prolist ul{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	overflow: auto;
	white-space: nowrap;
	border-bottom: 1px solid #f0f0f0;
}
.prolist ul li{
	padding: 0.266667rem;
	text-align: center;
}
.pro-img img{
	width: 2.133333rem;
}
.now-price{
	color: #ff3d00;
	font-size: 0.426667rem;
}
.old-price{
	color: #9c9a9a;
	text-decoration: line-through;
}
.new-products ul:after{
	content: '';
	display: block;
	clear: both;
}
.new-products ul li{
	float: left;
	width: 25%;
	height: 3.333333rem;
	border: 1px solid #f0f0f0;
	box-sizing: border-box;
}
.new-products ul li:nth-of-type(1){
	width: 50%;
	height: 6.666667rem;
}
.new-products ul li a{
	width: 100%;
	display: inline-block;
	position: relative;
}
.new-products ul li img{
	width: 100%;
}
.new-products ul li img:nth-of-type(even){
	width: 80%;
	position: absolute;
	top: 0.8rem;
	left: 10%;
}
.new-products ul li:nth-child(1) img:nth-of-type(2){
	position: absolute;
	top: 2.133333rem;
}



