
.cate-list{
	width: 75%;
	position: absolute;
	right: 0;
	top: 0;
	bottom: 0;
	overflow: auto;
}
.cate-list ul{
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}
.cate-list ul li{
	width: 33.33%;
	text-align: center;
	padding: 0.133333rem;
	box-sizing: border-box;
}
.cate-list ul li a{
	display: block;
	width: 100%;
	height: 100%;
}
.cate-list img{
	width: 90%;
	margin: 5%;
}
.catelist-name{
	color: #555;
	font-size: 0.373333rem;
}



