
.swiper-pagination-bullet{
	width: 0.2rem;
	height: 0.2rem;
	border-radius: 50%;
	background: #000;
	opacity: 0.3;
}
.swiper-pagination-bullet-active{
	width: 0.4rem;
	height: 0.2rem;
	border-radius: 0.1rem;
	background: #ff3d00;
	opacity: 1;
	transition: .5s;
}
.swiper-slide img{
	width: 100%;
}



