
.cate-menu{
	width: 25%;
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	background: #efefef;
}
.cate-menu ul li{
	height: 1rem;
	line-height: 1rem;
	color: #555;
	text-align: center;
	font-size: 0.373333rem;
}
.li-active{
	background: #fff;
	transition: 0.5s;
}



