
#hotsell{
	position: absolute;
	top: 0;
	bottom: -1.2rem;
	left: 0;
	right: 0;
	background: #fff;
	overflow:auto;
	z-index: 200;
}
.hotsell-menu{
	width: 100%;
	position: absolute;
	top: 1.093333rem;
}
.hotsell-menu ul{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
.hotsell-menu ul li{
	width: 25%;
	height: 1.066667rem;
	line-height: 1.066667rem;
	text-align: center;
	font-size: 0.426667rem;
	position: relative;
}
.hotsell-active:after{
	content: "";
    position: absolute;
    bottom: 0;
    height: 0.053333rem;
    width: 100%;
    left: 0;
    transition: all 2s;
    background: #ff3d00;
    z-index: 100;
}



