
#rank-list{
	width: 100%;
	position: absolute;
	top: 2.133333rem;
	bottom: 0;
	overflow: auto;
}
.list-title img{
	width: 100%;
}
.hotsell-items ul{
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}
.hotsell-items ul li{
	width: 50%;
	border-bottom: 0.106667rem solid #f0f0f0;
	box-sizing: border-box;
	padding: 0.133333rem;
}
.hotsell-items ul li:nth-of-type(odd){
	border-right: 0.106667rem solid #f0f0f0;
}
.hotsell-items ul li a{
	width: 100%;
	display: block;
	height: 100%;
	position: relative;
}
.hotsell-items img{
	width: 4.266667rem;
	height: 4.266667rem;
	margin: 5%;
}
.ranklist-name{
	color: #555;
	font-size: 0.373333rem;
	height: 0.853333rem;
	line-height: 0.426667rem;
	position: relative;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    word-break: break-all;
}
.market-price{
	margin-top: 0.266667rem;
	font-size: 0.32rem;
	color: #999;
	text-decoration: line-through;
}
.cost-prcie{
	color: #ff3d00;
	font-size: 0.32rem;
	margin-bottom: 0;
}
.hotsell-buy{
	width: 1.6rem;
	height: 0.64rem;
	line-height: 0.586667rem;
	color: #fff;
	text-align: center;
	border-radius: 0.106667rem; 
	position: absolute;
	right: 0.266667rem;
	bottom: 0.133333rem;
	background: #ff3d00;
}



