
#cart{
	width: 100%;
	position: absolute;
	top: 0;
	bottom: 0;
}
.cart-head{
	width: 100%;
	height: 1.2rem;
	position: absolute;
	top: 0;
	line-height: 1.2rem;
	border-bottom: 1px solid #ccc;
	box-sizing: border-box;
}
.cart-title{
	width: 40%;
	text-align: center;
	margin: 0 auto;
	font-size: 0.426667rem;
}
.editor{
	position: absolute;
	right: 0.533333rem;
	top: 0;
	color: #555;
	font-size: 0.373333rem;
}
.cart-content{
	width: 100%;
	position: absolute;
	top: 1.2rem;
	bottom: 0;
	overflow: auto;
}
.cart-content ul li{
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	border-bottom: 2px solid #f0f0f0;
}
.checkbox,.all-select{
	width: 15%;
}
.checkbox input,.all-select input{
	width: 0.533333rem;
	height: 0.533333rem;
	margin: 60% auto;
	display: block;
	border: none;
}
.cart-img{
	width: 25%;
}
.cart-img img{
	width: 90%;
	margin: 5%;
}
.pro-content{
	width: 60%;
	position: relative;
}
.pro-name{
	margin-top: 0.133333rem;
	line-height: 0.533333rem;
    height: 1.066667rem;
    font-size: 0.4rem;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    word-break: break-all;
}
.pro-price{
	color: #ff3d00;
	font-size: 0.4rem;
}
.pro-count{
	position: absolute;
	right: 0.266667rem;
	bottom: 0.266667rem;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
.pro-total{
	width: 0.666667rem;
	height: 0.666667rem;
	text-align: center;
	border: 1px solid #f0f0f0;
	outline: none;
	box-sizing: border-box;
}
.pro-reduce,.pro-add{
	width: 0.666667rem;
	height: 0.666667rem;
	border: 1px solid #f0f0f0;
	outline: none;
}
.cart-total{
	width: 100%;
	height: 1.2rem;
	position: absolute;
	bottom: 0;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	z-index: 100;
}
.all-select input{
	margin: 20% auto;
}
.total-count{
	width: 20%;
	background: #ff3d00;
	color: #fff;
	font-size: 0.426667rem;
	line-height: 1.173333rem;
	text-align: center;
}
.total-price{
	width: 60%;
	text-align: right;
	font-size: 0.32rem;
	color: #999;
	line-height: 1.173333rem;
}
.total-price span{
	color: #ff3d00;
	font-size: 0.426667rem;
}



