
#app-bar{
	width: 100%;
	height: 1.333333rem;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	background: #263238;
}
.title{
	width: 75%;
	height: 1.333333rem;
	color: #fff;
	padding-left: 1.466667rem;
	line-height: 1.333333rem; 
	background: url(../../../assets/fonts/logo.png) no-repeat;
	background-size: 1.066667rem 1.066667rem;
	background-position: 0.266667rem 0.133333rem;
	box-sizing: border-box;
	font-size: 0.373333rem;
}
button{
	width: 25%;
	height: 1.333333rem;
	background: #e51c23;
	font-size: 16px;
	line-height: 1.333333rem;
	text-align: center;
	color: #fff;
	border: none;
	font-size: 0.426667rem;
	font-weight: 500;
}



