
.guess-title img{
	width: 100%;
}
.guess-list ul{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}
.guess-list ul li{
	width: 50%;
	border: 2px solid #f0f0f0;
	box-sizing: border-box;
	padding: 0.133333rem;
}
.guess-list ul li a{
	display: block;
	width: 100%;
	height: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
}
.guess-list ul li img{
	width: 90%;
	margin: 5%;
}
.guess-price{
	color: #ff3d00;
	font-size: 0.373333rem;
}
.guess-name{
	font-size: 0.373333rem;
	overflow: hidden;
   /* display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;*/
    /*word-break: break-all;*/
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #555;
    margin-bottom: 0.133333rem;
}



